	module.exports = {"data":{"markdownRemark":{"html":"<p>In addition to classes, each HTML element can also have an <code>id</code> attribute.</p>\n<p>There are several benefits to using id attributes, and you’ll learn more about them once you start using jQuery.</p>\n<p><code>id</code> attributes should be unique. Browsers won’t enforce this, but it is a widely agreed upon best practice. So please don’t give more than one element the same id attribute.</p>\n<p>Here’s an example of how you give your h2 element the id of cat-photo-app:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code><h2 id=\"cat-photo-app\"></code></pre>\n      </div>","fields":{"slug":"/certificates/set-the-id-of-an-element/"},"frontmatter":{"title":"Set the Id of an Element"}}},"pathContext":{"slug":"/certificates/set-the-id-of-an-element/"}}

